interface InformasiPasienProps {
  nama: string
  tanggalLahir: string
  noRM: string
  ruangan: string
}

export function InformasiPasien({
  nama,
  noRM,
  ruangan,
  tanggalLahir,
}: InformasiPasienProps) {
  return (
    <div className='mb-4 flex'>
      <div className='w-full border-y border-l border-r border-black px-2 py-1'>
        <div className='flex items-center justify-start gap-4'>
          <p className='w-[120px]'>Nama Pasien</p>
          <p>: {nama}</p>
        </div>
        <div className='flex items-center justify-start gap-4'>
          <p className='w-[120px]'>Tanggal Lahir</p>
          <p>: {tanggalLahir}</p>
        </div>
      </div>
      <div className='w-full border-y border-black border-r px-2 py-1'>
        <div className='flex items-center '>
          <p className='w-[180px]'>Nomor Rekam Medis </p>
          <p>: {noRM}</p>
        </div>
        <div className='flex items-center'>
          <p className='w-[180px]'>Ruangan</p>
          <p>: {ruangan}</p>
        </div>
      </div>
    </div>
  )
}
