import { Button } from '@/components/ui/button'
import { getPelayanan } from '@/services/pelayanan'
import { constants } from '@/utils/constants'
import { useQuery } from '@tanstack/react-query'
import { PasienDataTable } from './components/table/pasien-data-table'
import { pasienColumns } from './components/table/pasien-columns'
import { DetailPasien } from './components/detail-pasien'
import { usePasienStore } from '@/store/pasien.store'
import { getPasienByPelayanan } from '@/services/pasien'
import { LoaderCircle } from 'lucide-react'

interface PasienProps {
  bagian: string
  setBagian: (value: string) => void
}

export default function Pasien({ bagian, setBagian }: PasienProps) {
  // pasien store
  const pasienStore = usePasienStore((state) => state)

  const { data: list } = useQuery({
    queryKey: [constants.queryKey.pelayanan.list],
    queryFn: getPelayanan,
    staleTime: Infinity,
  })

  const {
    data: listPasien,
    isLoading,
    isError,
  } = useQuery({
    queryKey: [constants.queryKey.pasien.byPelayanan, bagian],
    queryFn: () => getPasienByPelayanan({ kd_bagian: bagian }),
  })

  return (
    <>
      <div className='pelayanan-container flex items-center gap-2 overflow-x-auto w-[calc(100vw-116px)] h-[50px] mb-4'>
        {list?.response.map((item) => (
          <Button
            onClick={() => setBagian(item.kd_bagian)}
            variant={bagian != item.kd_bagian ? 'outline' : undefined}
            key={item.kd_bagian}
            className='transition-all ease-in hover:-translate-y-1'
          >
            {item.bagian}
          </Button>
        ))}
      </div>

      {/* Tabel */}
      {isLoading ? (
        <div className='h-[calc(100vh-20rem)] flex flex-1 justify-center items-center flex-col gap-2'>
          <LoaderCircle className='animate-spin' />
          <p className='text-md'>Mohon tunggu sebentar..</p>
        </div>
      ) : isError ? (
        <div className='h-[calc(100vh-20rem)] flex flex-1 justify-center items-center flex-col gap-2'>
          <p className='text-md text-red-500'>{constants.message.error}</p>
        </div>
      ) : (
        <PasienDataTable
          selectPasien={(pasien) => {
            pasienStore.setPasien(pasien)
          }}
          columns={pasienColumns}
          data={listPasien?.response ?? []}
        />
      )}
      {/* Tabel */}

      {/* Detail */}
      <DetailPasien
        open={pasienStore.pasien != null}
        setOpen={() => console.log('modal detail pasien closed.')}
      />
      {/* Detail */}
    </>
  )
}
