import { Dialog, DialogContent } from '@/components/ui/dialog'
import { DialogDescription, DialogTitle } from '@radix-ui/react-dialog'
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar'

import { Button } from '@/components/ui/button'
import { useState } from 'react'
import { CPPT } from './cppt'
import { AsesmenKeperawatanDewasa } from './asesmen-keperawatan-dewasa'
import { AsesmenKeperawatanAnak } from './asesmen-keperawatan-anak'
import { ResikoJatuh } from './resiko-jatuh'
import { AnalisaData } from './analisa-data'
import { KontrolPasien } from './kontrol-pasien'
import { InstruksiMedisFarmakologi } from './instruksi-medis-farmakologi'
import { MonitoringCairan } from './monitoring-cairan'
import { LoaderCircle, Printer, Sidebar } from 'lucide-react'
import { constants } from '@/utils/constants'
import { usePasienStore } from '@/store/pasien.store'
import { Link } from '@tanstack/react-router'
import { CpptAddModal } from './modal/cppt-add-modal'
import { useAuthStore } from '@/store/auth.store'
import { useGlobalStore } from '@/store/global.store'
import UpdateSoap from './modal/update-soap'
import UpdateSbar from './modal/update-sbar'

interface DetailPasienProps {
  open: boolean
  setOpen: () => void
}

export function DetailPasien({ open, setOpen }: DetailPasienProps) {
  // store
  const { user } = useAuthStore((state) => state)
  const { pasien, setPasien } = usePasienStore((state) => state)
  const { showAddDialog, dialogId, setDialogId, setShowAddDialog } =
    useGlobalStore((state) => state)

  // state
  const [showSideBar, setShowSideBar] = useState(true)

  // mock fetching data
  const [
    loading,
    //  setLoading
  ] = useState(false)

  // selected menu
  const [
    menu,
    // setMenu
  ] = useState<Menu[]>(initalMenu)
  const [selectedMenu, setSelectedMenu] = useState<Menu>(Menu.CPPT)

  // to report path
  const to = selectedMenu.replace(/\s+/g, '_').toLowerCase()

  return (
    <>
      {/* Dialog Detail Pasien  */}
      <Dialog
        modal
        open={open}
        onOpenChange={() => {
          setSelectedMenu(Menu.CPPT)
          setOpen()
          setPasien(null)
        }}
      >
        <DialogTitle></DialogTitle>
        <DialogDescription></DialogDescription>
        <DialogContent className='min-h-screen max-w-screen-2xl'>
          <div className='flex'>
            {/* Left Content */}
            {showSideBar && (
              <div className='w-[250px] border p-4'>
                {/* Profile Pasien */}
                <div className='h-[200px] border flex flex-col justify-center items-center text-center'>
                  <Avatar className='h-[100px] w-[100px]'>
                    <AvatarImage src={''} alt='pasien photos' />
                    <AvatarFallback className='text-2xl'>
                      {pasien?.nama_pasien[0]}
                    </AvatarFallback>
                  </Avatar>
                  <h3 className='py-2'>{pasien?.nama_pasien}</h3>
                </div>
                {/* Profile Pasien */}
                <div className='h-[350px] w-full p-2 overflow-x-scroll'>
                  {Object.values(menu).map((item, i) => (
                    <Button
                      variant={selectedMenu != item ? 'outline' : 'default'}
                      className='w-full mb-2 hover:translate-x-1 transition-all ease-in-out text-[10px] font-semibold'
                      key={i}
                      onClick={() => {
                        setSelectedMenu(item)
                        // setShowSideBar((prev) => !prev)
                      }}
                    >
                      {item}
                    </Button>
                  ))}
                </div>
              </div>
            )}
            {/* Left Content */}
            {/* Right Content */}
            <div className='w-full border h-[calc(100vh-24px)] overflow-y-scroll'>
              {/* AppBar */}
              <div className='p-2 w-full bg-blue-500 flex items-center justify-between'>
                <div className='flex items-center gap-2'>
                  <Sidebar
                    className='text-white hover:cursor-pointer'
                    onClick={() => setShowSideBar((prev) => !prev)}
                  />
                  <h3 className='text-lg font-medium text-white'>
                    {selectedMenu == 'CPPT'
                      ? 'CATATAN PERKEMBANGAN PASIEN TERINTEGRASI'
                      : selectedMenu}
                  </h3>
                </div>
                <Link to={`/report/${to}`} target='_blank'>
                  <Printer className='text-white mr-10 hover:cursor-pointer' />
                </Link>{' '}
              </div>
              {/* AppBar */}

              <div className='p-2'>
                {loading ? (
                  <div className='h-[calc(100vh-100px)] flex flex-col items-center justify-center text-center'>
                    <LoaderCircle className='animate-spin mb-2' />
                    <p>{constants.message.loading}</p>
                  </div>
                ) : (
                  render(selectedMenu)
                )}
              </div>
            </div>
            {/* Right Content */}
            {/* FAB */}
            <div className='relative'>
              {pasien?.bagian !== user?.bagian && (
                <Button
                  onClick={() => {
                    setShowAddDialog(true)
                    setDialogId('dialog-pasien-add-data')
                  }}
                  className='absolute right-0 bottom-[20px]'
                >
                  Tambah Data
                </Button>
              )}
            </div>
            {/* FAB */}
          </div>
        </DialogContent>
      </Dialog>
      {/* Dialog Detail Pasien  */}

      {/* Dialog Tambah Data  */}

      <Dialog
        modal
        open={showAddDialog}
        onOpenChange={() => {
          setShowAddDialog(!showAddDialog)
          setDialogId('')
        }}
      >
        <DialogTitle></DialogTitle>
        <DialogDescription></DialogDescription>
        <DialogContent className='min-h-screen max-w-screen-2xl'>
          {dialogId == 'dialog-pasien-add-data' &&
            selectedMenu == Menu.CPPT && <CpptAddModal />}
          {dialogId == 'dialog-update-cppt-soap' &&
            selectedMenu == Menu.CPPT && <UpdateSoap />}
          {dialogId == 'dialog-update-cppt-sbar' &&
            selectedMenu == Menu.CPPT && <UpdateSbar />}
        </DialogContent>
      </Dialog>

      {/* Dialog Tambah Data  */}
    </>
  )
}

enum Menu {
  CPPT = 'CPPT',
  ASESMEN_KEPERAWATAN_DEWASA = 'ASESMEN KEPERAWATAN DEWASA',
  ASESMEN_KEPERAWATAN_ANAK = 'ASESMEN KEPERAWATAN ANAK',
  RESIKO_JATUH = 'RESIKO JATUH',
  ANALISA_DATA = 'ANALISA DATA',
  KONTROL_PASIEN = 'KONTROL PASIEN',
  INSTRUKSI_MEDIS_FARMAKOLOGI = 'INSTRUKSI MEDIS FARMAKOLOGI',
  MONITORING_CAIRAN = 'MONITORING CAIRAN',
}

const initalMenu = [
  Menu.CPPT,
  Menu.ASESMEN_KEPERAWATAN_DEWASA,
  Menu.ASESMEN_KEPERAWATAN_ANAK,
  Menu.RESIKO_JATUH,
  Menu.ANALISA_DATA,
  Menu.KONTROL_PASIEN,
  Menu.INSTRUKSI_MEDIS_FARMAKOLOGI,
  Menu.MONITORING_CAIRAN,
]

function render(menu: Menu) {
  switch (menu) {
    case Menu.CPPT:
      return <CPPT />
    case Menu.ASESMEN_KEPERAWATAN_DEWASA:
      return <AsesmenKeperawatanDewasa />
    case Menu.ASESMEN_KEPERAWATAN_ANAK:
      return <AsesmenKeperawatanAnak />
    case Menu.RESIKO_JATUH:
      return <ResikoJatuh />
    case Menu.ANALISA_DATA:
      return <AnalisaData />
    case Menu.KONTROL_PASIEN:
      return <KontrolPasien />
    case Menu.INSTRUKSI_MEDIS_FARMAKOLOGI:
      return <InstruksiMedisFarmakologi />
    case Menu.MONITORING_CAIRAN:
      return <MonitoringCairan />
    default:
      return <CPPT />
  }
}
