import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from '@/components/ui/alert-dialog'
import { useGlobalStore } from '@/store/global.store'

export function Alert() {
  const state = useGlobalStore((state) => state)

  return (
    <AlertDialog open={state.showModal}>
      <AlertDialogContent>
        <AlertDialogHeader>
          <AlertDialogTitle>Message</AlertDialogTitle>
          <AlertDialogDescription>
            {state.descriptionModal}
          </AlertDialogDescription>
        </AlertDialogHeader>
        <AlertDialogFooter
          onClick={() => {
            state.setShowModal(false)
            state.setShowAddDialog(false)
          }}
        >
          <AlertDialogAction className='w-full'>OK</AlertDialogAction>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
  )
}
