import Error from '@/components/error'
import { InformasiPasien } from '@/components/informasi_pasien'
import { LayoutBorderNo } from '@/components/layout_border_no'
import Loading from '@/components/loading'
import { cn } from '@/lib/utils'
import { getReportCppt } from '@/services/cppt'
import { useGlobalStore } from '@/store/global.store'
import { usePasienStore } from '@/store/pasien.store'
import { constants } from '@/utils/constants'
import { useQuery } from '@tanstack/react-query'
import { Edit } from 'lucide-react'
import { ReactNode } from 'react'
import QRCode from 'react-qr-code'

interface CPPTProps {
  isReport?: boolean
}

export function CPPT({ isReport = false }: CPPTProps) {
  const { setDialogId, setShowAddDialog, setItem } = useGlobalStore(
    (state) => state
  )
  const { pasien } = usePasienStore((state) => state)

  const { data, isLoading, isError } = useQuery({
    queryKey: [constants.queryKey.pasien.cppt.report],
    queryFn: () => getReportCppt(pasien!.no_rm),
  })

  if (isLoading) return <Loading />
  if (isError) return <Error />

  return (
    <LayoutBorderNo no='RM.RI ?' isReport={isReport}>
      {/* Content */}
      <div className='p-2'>
        <h1 className='text-center text-black font-semibold mb-4'>
          CATATAN PERKEMBANGAN PASIEN TERINTEGRASI
        </h1>

        {/* Informasi Pasien */}
        {pasien != null && (
          <InformasiPasien
            nama={pasien.nama_pasien}
            noRM={pasien.no_rm}
            ruangan={pasien.bagian}
            tanggalLahir={pasien.tgl_lahir}
          />
        )}
        {/* Informasi Pasien */}

        <div>
          {/* Header */}
          <div className='flex text-xs'>
            <TH name='Tanggal & Jam' className='w-[100px]' />
            <TH name='Professional Pemberi Asuhan' className='w-[150px]' />
            <TH name='Hasil Asesmen' />
            <TH name='Instruksi PPA' className='w-[150px]' />
            <TH
              name='Verifikasi DPJP'
              isLast={isReport ? true : false}
              className='w-[150px]'
            />
            {!isReport && <TH name='Action' isLast className='w-[100px]' />}
          </div>
          {/* Header */}

          {/* Body */}

          {data?.response?.map((item) => (
            <div className='flex text-xs text-wrap' key={item.id}>
              <TH name={item.tanggal} className='w-[100px]' />
              <TH name={item.pemberi_asuhan} className='w-[150px]' />
              <TH name={item.cppt} />
              <TH name={item.instruksi_ppa} className='w-[150px]' />
              <TH
                name={item.dpjp}
                hasQR
                isLast={isReport}
                className='w-[150px]'
              />
              {!isReport && (
                <TH
                  isChild={
                    <Edit
                      className='hover:cursor-pointer'
                      onClick={() => {
                        setShowAddDialog(true)
                        console.log('selected cppt item', item)
                        setItem(item)
                        if (item.cppt.includes('Subjektif')) {
                          setDialogId('dialog-update-cppt-soap')
                        } else {
                          setDialogId('dialog-update-cppt-sbar')
                        }
                      }}
                    />
                  }
                  isLast
                  className='w-[100px]'
                />
              )}
            </div>
          ))}
          {/* Body */}
        </div>
      </div>
      {/* Content */}
    </LayoutBorderNo>
  )
}

function TH({
  name,
  isLast,
  className = 'flex-1',
  hasQR = false,
  isChild,
}: {
  name?: string
  isLast?: boolean
  className?: string
  hasQR?: boolean
  isChild?: ReactNode
}) {
  if (isChild)
    return (
      <div
        className={cn(
          'flex flex-col gap-2 text-center justify-center items-center p-2 border-y border-black border-l',
          isLast && 'border-r',
          className
        )}
      >
        {isChild}
      </div>
    )

  return (
    <div
      className={cn(
        'flex flex-col gap-2 text-center justify-center items-center p-2 border-y border-black border-l',
        isLast && 'border-r',
        className
      )}
    >
      {hasQR && <QRCode value={name!} className='h-[50px] w-[50px]' />}
      {name != '' ? <p>{name}</p> : null}
    </div>
  )
}
