import { LayoutDashboard, Users } from 'lucide-react'
import { Button } from './ui/button'

interface SidebarProp {
  index: number
  setIndex: (index: number) => void
}

export default function Sidebar({ index, setIndex }: SidebarProp) {
  return (
    <section className='h-[calc(100vh-56px)] w-[100px] border  p-4 flex flex-col items-center justify-between'>
      <div className='w-full flex flex-col gap-2'>
        <Button
          variant={index == 0 ? undefined : 'outline'}
          className='w-full flex justify-center hover:translate-x-1 transition-all ease-in-out'
          onClick={() => setIndex(0)}
        >
          <LayoutDashboard />
        </Button>

        <Button
          variant={index == 1 ? undefined : 'outline'}
          className='w-full flex justify-center hover:translate-x-1 transition-all ease-in-out'
          onClick={() => setIndex(1)}
        >
          <Users />
        </Button>
      </div>
    </section>
  )
}
