import CardDashboard from '@/components/card_dashboard'

interface DashboardProps {
  count: number
  onClickJumlahPasien: () => void
}

export default function Dashboard({
  count,

  onClickJumlahPasien,
}: DashboardProps) {
  return (
    <div className='w-[calc(100vw-116px)] grid grid-cols-1 md:grid-cols-3 gap-2'>
      <CardDashboard
        title='Jumlah Pasien'
        count={count}
        onClick={onClickJumlahPasien}
      />
    </div>
  )
}
