import { ReactNode } from 'react'
import Header from './header'
import { Toaster } from '@/components/ui/toaster'
import { Alert } from './alert'

const Layout = ({
  children,
  showHeader = false,
}: {
  children: ReactNode
  showHeader?: boolean
}) => {
  return (
    <>
      {showHeader && <Header />}
      <div className='max-width'>{children}</div>
      <Alert />
      <Toaster />
    </>
  )
}
export default Layout
