import { useGlobalStore } from '@/store/global.store'
import { useAuthStore } from '@/store/auth.store'
import { useToast } from '../use-toast'
import { useState } from 'react'
import { Meta } from '@/type'
import { z } from 'zod'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import { login } from '@/services/auth'
import { constants } from '@/utils/constants'

export function useLogin() {
  const authState = useAuthStore((state) => state)
  const globalState = useGlobalStore((state) => state)

  const { toast } = useToast()

  const [status, setStatus] = useState<'loading' | ''>('')
  const [err, setErr] = useState<Meta | null>(null)

  const formSchema = z.object({
    email: z.string().email(),
    password: z.string().min(3, 'password length min 3 character'),
  })

  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      email: 'tabitagultom@gmail.com',
      password: '12345',
    },
  })

  async function onSubmit(values: z.infer<typeof formSchema>) {
    try {
      setStatus('loading')
      const response = await login(values)
      if (response.metadata.code == 201) {
        // error from backend, dont ask me why
        setErr(response.metadata)
        globalState.setDescription(response.metadata.message)
        globalState.setShowModal(true)
        setStatus('')
        return
      }

      authState.setIsLogin(true)
      authState.setUser(response.response)
      setStatus('')
    } catch (error) {
      console.log('error on login', error)
      toast({
        variant: 'destructive',
        title: constants.message.error,
        description: err?.message ?? 'error',
      })
      setStatus('')
    }
  }

  return { authState, status, form, onSubmit }
}
