import Error from '@/components/error'
import Layout from '@/components/layout'
import Loading from '@/components/loading'
import Sidebar from '@/components/sidebar'
import Dashboard from '@/features/dashboard/dashboard'
import Pasien from '@/features/pasien/pasien'
import { getDashboard } from '@/services/dashboard'
import { useAuthStore } from '@/store/auth.store'
import { constants } from '@/utils/constants'
import { useQuery } from '@tanstack/react-query'
import { createFileRoute } from '@tanstack/react-router'
import { useState } from 'react'

export const Route = createFileRoute('/')({
  component: Home,
})

function Home() {
  const authState = useAuthStore((state) => state)

  if (!authState.isLogin) {
    // if not logged in, navigate to login page
    window.location.replace('/login')
  }

  // selected tab
  const [index, setIndex] = useState(0)
  // selected bagian
  const [bagian, setBagian] = useState<string | undefined>(
    authState.user?.kd_bagian
  )

  const {
    data: dashboard,
    isLoading,
    isError,
  } = useQuery({
    queryKey: [constants.queryKey.dashboard, bagian],
    queryFn: () => getDashboard({ kd_bagian: bagian! }),
  })

  return (
    <Layout showHeader>
      <div className='flex'>
        {/* Sidebar */}
        <Sidebar
          index={index}
          setIndex={(i) => {
            setIndex(i)
            setBagian(authState.user?.kd_bagian)
          }}
        />
        {/* Sidebar */}

        {/* Main Content */}
        <main className='h-[calc(100vh-56px)] overflow-y-scroll w-full transition-all p-4'>
          <h1 className='font-semibold  text-3xl mb-2'>
            {index == 0 ? 'Dashboard' : 'Pasien'}
          </h1>
          {index == 0 ? (
            <>
              {isLoading ? (
                <Loading />
              ) : isError ? (
                <Error />
              ) : (
                <Dashboard
                  count={dashboard?.response.jumlah_pasien ?? 0}
                  onClickJumlahPasien={() => setIndex(1)}
                />
              )}
            </>
          ) : (
            <Pasien bagian={bagian!} setBagian={(value) => setBagian(value)} />
          )}
        </main>
        {/* Main Content */}
      </div>
    </Layout>
  )
}
