import { DialogId } from '@/type'
import { create } from 'zustand'

interface GlobalState {
  showModal: boolean // modal for success/error after receiveing response from  api
  descriptionModal: string
  showAddDialog: boolean // for insert data
  dialogId: DialogId
  item: unknown // for update data
  setShowModal: (val: boolean) => void
  setDescription: (val: string) => void
  setShowAddDialog: (val: boolean) => void
  setDialogId: (val: DialogId) => void
  setItem: (val: unknown) => void
}

export const useGlobalStore = create<GlobalState>((set) => ({
  showModal: false,
  descriptionModal: '',
  dialogId: '',
  showAddDialog: false,
  item: null,
  setShowModal: (val: boolean) => set(() => ({ showModal: val })),
  setDescription: (val: string) => set(() => ({ descriptionModal: val })),
  setShowAddDialog: (val: boolean) => set(() => ({ showAddDialog: val })),
  setDialogId: (val: DialogId) => set(() => ({ dialogId: val })),
  setItem: (val: unknown) => set(() => ({ item: val })),
}))
